* {
  margin: 0;
  padding: 0;
}

*,*:focus,*:hover{
  outline:none;
}

.App {
  margin-top: 0;
  background-color: #331E36;
  color: white;
}

.feed {
  width:60%;
}

.detailView {
  width: 40%;
  margin-left: 60%;
  position: fixed;
  overflow-y: auto;
  top: 40px;
  bottom: 0;
  scrollbar-width:none;
}

.detailView::-webkit-scrollbar {
  display: none;
}

.tweet {
  min-height: 300px;
  border: 1px solid #331E36;
  border-top: none;
  background-color: #C2EFEB;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-height: 500px;
}

.tweet, .reply, .retweet, .retweetError {
  word-wrap: break-word;
}

.detailView .tweet {
  border-left: none;
}

.tweetHead {
  display: flex;
  align-items: center;
}
.tweetHead * {
  margin-top: 10px;
  margin-left: 10px;
}
.tweetHead .rt {
  color: #41337A;
}

.tweetBody {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.tweetBody p {
  margin-left: 5%;
}

.retweet {
  background-color: #ECFEE8;
  width: 90%;
  min-height: 150px;
  margin: auto;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.pfp {
  width: 55px;
  height: 55px;
  
  object-fit: cover;
  border-radius: 50%;
}

.tweet button, .reply button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  padding: 2px;
}

.tweetMenu {
  display: flex;
  justify-content: space-between;
}

.tweetMenu > div {
  display: flex;
  justify-content: flex-start;
}

.tweetMenu * {
  margin-left: 10px;
}

.tweet button:hover {
  background-color: #6EA4BF;
  border-radius: 2px;
}

.reply button:hover {
  background-color: #C2EFEB;
  border-radius: 2px;
}

.tweetMenu .unLikeButton {
  color: #41337A;
  font-weight: bold;
}

.body {
  margin-top: 40px;
  display:flex;
  flex-direction: row;
}

.reply {
  background-color: #6EA4BF;
  margin-left: 30px;
  border: 1px solid black;
  border-top: none;
  color: black;
}

header {
  top:0;
  position: fixed;
  display: block;
  z-index: 1;
  background-color: #331E36;
  width: 100%;
  height: 40px;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

header .pfp {
  height: 35px;
  width: 35px;
}

header > div {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

header button {
  border: none;
  background-color: transparent;
  font-family: inherit;
  cursor: pointer;
  color: white;
  padding: 2px;
}

header button:hover {
  background-color: #C2EFEB;
  border-radius: 2px;
  color: black;
}

header > div * {
  margin-right: 10px;
}

header h1 {
  margin-left: 11px;
  cursor: pointer;
}

header h1:hover {
  color: #C2EFEB;
}

.tweetMenu p {
  font-weight: lighter;
  margin-right: 10px;
}

.composeHeader {
  padding: 10px;
}

.composeTextArea {
  resize: none;
  outline: none;
  border-radius: 5px;
  padding: 5px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  height: 100px;
  background-color: #ECFEE8;
  width: 90%;
  margin: auto;
  display: block;
}

.composeMenu {
  margin: 10px;
  display: flex;
  justify-content: space-between;
}

.composeMenu .postButton {
  border: 1px solid black;
  border-radius: 2px;
  font-size: large;
  margin-left: 10px;
}

.composeMenu .postButton:hover {
  box-shadow: none;
}

.tweet .deleteButton, .tweet .editButton {
  font-weight: bold;
  color: #41337A;
}

.fail {
  display: block;
  position: fixed;
  z-index: 1;
  width: 30%;
  margin-left: 60%;
  top: 0px;
  background-color: #6EA4BF;
  height: 40px;
}

.fail h1 {
  padding: 2px;
}